}
/*FAVORITE COURESES */
.favourite_courses{
  flex-wrap: wrap;
  justify-items: center;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 20px;
}
.Fav_courses {
 width: 400px;
 flex-wrap: wrap;
  background-color: white;
  text-align: center;
  border-radius: 30px;
}
